export class ChromeService {
    injectApp() {
        let iframe = document.createElement('iframe');
        iframe.src = `${this.extensionOrigin}/${this.extensionWebAccessibleResources[0]}`;
        iframe.style.cssText = 'position:fixed;top:0;left:0;display:block;' +
            'width:270px;height:100%;z-index:1000;box-shadow: 3px 0 6px rgba(0,0,0,0.24);border-width:0px;';

        document.head.parentNode.insertBefore(iframe, document.head.nextSibling);
        let html = document.getElementsByTagName('html');
        html[0].style.marginLeft = '270px';
    }

    get extensionOrigin(): string {
        return `chrome-extension://${chrome.runtime.id}`;
    }

    get extensionManifest(): any {
        return chrome.runtime.getManifest();
    }

    get extensionWebAccessibleResources(): any {
        return this.extensionManifest.web_accessible_resources;
    }
}


